@import "../../../variables.scss";

.root {
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.title {
  padding: 32px 16px;
  text-align: center;
}

.paper {
  overflow: hidden;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 54px;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  background: #193F3F;

  .items {
    overflow-y: scroll;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    grid-gap: 12px;
    margin-top: 16px;
  }
}

.bottom {
  width: 60%;
  align-self: flex-end;
  padding: 16px;
  margin: 0 auto;
  text-align: center;
}