@import "../../../variables";

.root {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  justify-items: center;
  height: 100vh;
  padding: 1rem;
}

.title {
  margin-bottom: 1.5rem;
  text-align: center;
  line-height: 1.8;
}

.items {
  width: 65%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .item {
    width: 100%;
    margin-top: 1rem;

    &:first-of-type {
      margin-top: 0;
    }
  }
}

.bottom {
  align-self: flex-end;
}